<template>
    <div class="show-message">
        <div v-if="$route.name === '消息管理'">
            <Button type="success" size="large" to="/manage/msg_view/guanli/create" class="create">添加客服消息</Button>
            <RadioGroup v-model="sort" type="button" @on-change="showMessage">
                <Radio label="默认"></Radio>
                <Radio label="定时时间"></Radio>
            </RadioGroup>
            <Button type="warning" size="large" @click="modal = true" style="float: right;">按时间段删除</Button>
            <Table class="message-table" :columns="messageHeader" :data="messageData"></Table>
            <Page style="margin-top:30px;" show-total :total="dataList.length" :page-size="10" @on-change="changePage" />
            <div class="modal" v-if="modal">
              <h3>请选择删除时间段</h3><br>
              <DatePicker type="datetime" placeholder="选择开始时间" style="width: 200px;margin-bottom: 20px;" v-model="startTime"></DatePicker>
              <DatePicker type="datetime" placeholder="选择结束时间" style="width: 200px" v-model="endTime"></DatePicker>
              <Button class="btn" type="warning" @click="modalClose">取消</Button>
              <Button class="btn" type="success" @click="modalSubmit">确定</Button>
            </div>
        </div>
        <router-view />
    </div>
</template>

<script>
export default {
  data() {
    return {
      sort: "默认",
      modal: false,
      startTime: "",
      endTime: "",
      dataList: [],
      messageData: [],
      messageHeader: [
        {
          key: "type",
          title: "类型",
          align: "center",
          render: (h, params) => {
            let type =
              (params.row.type == 1 ? "文本消息" : "图文消息") +
              (params.row.contents.length > 1 ? "(多)" : "");
            return h(
              "span",
              {
                props: {}
              },
              type
            );
          }
        },
        {
          key: "task",
          title: "是否延时",
          align: "center",
          render: (h, params) => {
            let task = params.row.task == true ? "是" : "否";
            return h(
              "span",
              {
                props: {}
              },
              task
            );
          }
        },
        {
          key: "delay",
          title: "延时时间(分钟)",
          align: "center",
          render: (h, params) => {
            let delay = params.row.task == true ? params.row.delay : "——";
            return h(
              "span",
              {
                props: {}
              },
              delay
            );
          }
        },
        {
          key: "is_timing",
          title: "是否定时(分钟)",
          align: "center",
          render: (h, params) => {
            let is_timing = params.row.is_timing == true ? "是" : "否";
            return h(
              "span",
              {
                props: {}
              },
              is_timing
            );
          }
        },
        {
          key: "timing_time",
          title: "定时时间",
          align: "center",
          width: 100,
          render: (h, params) => {
            let timing_time =
              params.row.is_timing == true ? params.row.time : "——";
            return h(
              "span",
              {
                props: {}
              },
              timing_time
            );
          }
        },
        {
          key: "sex",
          title: "性别",
          align: "center",
          width: 100,
          render: (h, params) => {
            let sex =
              params.row.sex == "0"
                ? "未知"
                : params.row.sex == "1"
                  ? "男"
                  : params.row.sex == "2"
                    ? "女"
                    : "全部";
            return h(
              "span",
              {
                props: {}
              },
              sex
            );
          }
        },
        {
          key: "title",
          title: "标题",
          align: "center",
          tooltip: true,
          render: (h, params) => {
            let title = params.row.contents[0].title || "——";
            return h(
              "Tooltip",
              {
                props: {
                  placement: "top-start",
                  content: title,
                  maxWidth: 200
                }
              },
              [
                h(
                  "span",
                  {
                    style: {
                      diaplay: "inline-block",
                      width: "100%",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                      wordBreak: "break-all"
                    }
                  },
                  title
                )
              ]
            );
          }
        },
        {
          key: "description",
          title: "详细信息",
          align: "center",
          tooltip: true,
          width: 90,
          render: (h, params) => {
            let description = params.row.contents[0].description || "——";
            return h(
              "Tooltip",
              {
                props: {
                  placement: "top-start",
                  content: description,
                  maxWidth: 200
                }
              },
              [
                h(
                  "span",
                  {
                    style: {
                      diaplay: "inline-block",
                      width: "100%",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                      wordBreak: "break-all"
                    }
                  },
                  description
                )
              ]
            );
          }
        },
        {
          key: "url",
          title: "链接",
          align: "center",
          tooltip: true,
          render: (h, params) => {
            let url = params.row.contents[0].url || "——";
            return h(
              "Tooltip",
              {
                props: {
                  placement: "top-start",
                  content: url,
                  maxWidth: 200
                }
              },
              [
                h(
                  "span",
                  {
                    style: {
                      diaplay: "inline-block",
                      width: "100%",
                      overflow: "hidden",
                      textOverflow: "ellipsis",
                      whiteSpace: "nowrap",
                      wordBreak: "break-all"
                    }
                  },
                  url
                )
              ]
            );
          }
        },
        {
          key: "img",
          title: "图片",
          width: 160,
          align: "center",
          render: (h, params) => {
            return h("img", {
              attrs: {
                src: params.row.img,
                style: "width: 100%; padding: 10px;"
              }
            });
          }
        },
        {
          title: "Action",
          align: "center",
          width: 250,
          render: (h, params) => {
            let that = this;
            return h("div", [
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "warning",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.copyMessage(params.row);
                    }
                  }
                },
                "复制"
              ),
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px"
                  },
                  props: {
                    type: "primary",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.$router.push({
                        name: "修改客服消息",
                        params: params.row
                      });
                    }
                  }
                },
                "修改"
              ),
              h(
                "Button",
                {
                  style: {
                    marginRight: "10px",
                    display:
                      !params.row.task && !params.row.is_timing
                        ? "inline-block"
                        : "none"
                  },
                  props: {
                    type: "success",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.sendMessage(params.row);
                    }
                  }
                },
                "发送消息"
              ),
              h(
                "Button",
                {
                  props: {
                    type: "error",
                    size: "small"
                  },
                  on: {
                    click() {
                      that.deleteMessage(params.row);
                    }
                  }
                },
                "删除"
              )
            ]);
          }
        }
      ]
    };
  },
  mounted() {
    this.showMessage();
  },
  methods: {
    copyMessage(row) {
       this.$axios
          .post("/message/create", {
            contents: row.contents,
            type: row.type,
            task: row.task,
            is_timing: row.is_timing,
            delay: row.delay,
            timing_time: row.timing_time,
            codes: row.codes,
            img: row.img,
            tagId: row.tagId,
            sex: row.sex
          })
          .then(res => {
            if (res.data.success) {
              let data = res.data.data
              data.time = this.formatDate(data.timing_time)
              this.messageData.unshift(data);
              this.$Message.info(res.data.success);
            } else {
              this.$Message.info(res.data.err);
            }
          });
    },
    showMessage() {
      if(this.sort === "默认") {
        this.$axios("/message", {params: {sort: "_id"}}).then(res => {
          if (res.data.messages.length != 0) {
            this.dataList = res.data.messages;
            this.messageData = this.dataList.slice(0, 10)
          }
        });
      } else if(this.sort === "定时时间") {
        this.$axios("/message", {params: {sort: "timing_time"}}).then(res => {
          if (res.data.messages.length != 0) {
            this.dataList = res.data.messages;
            this.messageData = this.dataList.slice(0, 10)
          }
        });
      }
    },
    sendMessage(row) {
      this.$Modal.confirm({
        title: "提示:",
        content: "确认发送这条消息吗？",
        onOk: () => {
          this.$axios
            .get("/message/send", {
              params: {
                id: row._id,
                take_over: true,
                tagId: row.tagId
              }
            })
            .then(res => {
              this.$Message.info(res.data.success);
            });
        }
      });
    },
    deleteMessage(row) {
      this.$Modal.confirm({
        title: "提示:",
        content: "确认删除这条内容吗？",
        onOk: () => {
          this.$axios
            .get("/message/delete", { params: { id: row._id } })
            .then(res => {
              if (res.data.data == 0) {
                this.$Message.info("已删除最后一条数据");
                this.messageData = [];
              } else {
                this.$Message.info(res.data.success);
                this.showMessage();
              }
            });
        }
      });
    },
    changePage(index){
      var _start = ( index - 1 ) * 10;
      var _end = index * 10;
      this.messageData = this.dataList.slice(_start,_end);
      this.goTop()
    },
    goTop () {
      let self = this
      self.timer = setInterval(function () {
        let osTop = document.documentElement.scrollTop || document.body.scrollTop
        let ispeed = Math.floor(-osTop)
        document.documentElement.scrollTop = document.body.scrollTop = osTop + ispeed
        self.isTop = true
        if (osTop === 0) {
          clearInterval(self.timer)
        }
      }, 30)
    },
    formatDate(time) {
      var date = new Date(time);
      var year = date.getFullYear(),
        month = date.getMonth() + 1,
        day = date.getDate(),
        hour = date.getHours(),
        min = date.getMinutes(),
        sec = date.getSeconds();
      var newTime =
        year +
        "-" +
        (month < 10 ? "0" + month : month) +
        "-" +
        (day < 10 ? "0" + day : day) +
        " " +
        (hour < 10 ? "0" + hour : hour) +
        ":" +
        (min < 10 ? "0" + min : min) +
        ":" +
        (sec < 10 ? "0" + sec : sec);
      return newTime;
    },
    modalClose() {
      this.modal = false;
      this.startTime = this.endTime = ""
    },
    modalSubmit() {
      this.$axios.get('/message/remove', {
        params: {
          startTime: +new Date(this.startTime),
          endTime: +new Date(this.endTime)
        }
      }).then(res => {
        this.$Message.info(res.data.success);
        this.modal = false;
        this.startTime = this.endTime = ""
        this.$router.go(0)
      })
    }
  },
  watch: {
    $route(to, from) {
      if (from.name == "添加客服消息" || from.name == "修改客服消息") {
        this.showMessage();
      }
    }
  }
};
</script>

<style scoped>
.create {
  margin-bottom: 30px;
  float: left;
}

.message-table {
  clear: both;
}

.modal {
  width: 300px;
  /* height: 180px; */
  padding: 30px;
  position: fixed;
  top: 50%;
  left: 50%;
  margin-left: -165px;
  margin-top: -105px;
  background: #eee;
  z-index: 100000;
  color: #999;
  text-align: left;
}
.modal .btn {
  margin-top: 30px;
  margin-right: 20px;
}
</style>